.home--section {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: initial;
      -ms-flex-pack: initial;
          justify-content: initial;
  -webkit-box-align: initial;
      -ms-flex-align: initial;
          align-items: initial;
  height: 95vh;
  width: 70vw;
  margin: auto;
  margin-top: 4rem;
}

.home__icons {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  color: #6256bf;
  font-size: 2rem;
  margin-right: 1rem;
}

.home__icons i, .home__icons a {
  margin: 1rem;
}

.home__content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: initial;
      -ms-flex-align: initial;
          align-items: initial;
  -webkit-box-flex: 2;
      -ms-flex: 2;
          flex: 2;
  padding: 0rem 5vw;
  padding-top: 9rem;
  padding-bottom: 7rem;
}

.home__content h1 {
  font-size: 3rem;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin-bottom: 0rem;
}

.home__content .position {
  margin: 0%;
}

.home__content .content__p {
  margin: 3rem 0rem;
  line-height: 1.6rem;
  font-size: 1.1rem;
}

#Contact_me {
  background-color: #6256bf;
  color: white;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 1.2rem 1rem;
  border: none;
  outline: none;
  font-size: 1.2rem;
  border-radius: 0.3rem;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}

#Contact_me i {
  margin: 0rem 0.5rem;
}

#Contact_me:hover {
  cursor: pointer;
  -webkit-transform: translateY(2px);
          transform: translateY(2px);
}

.home__content .footer {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-top: 4rem;
  -webkit-transition: all .2s;
  transition: all .2s;
}

.home__content .footer a {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: initial;
      -ms-flex-pack: initial;
          justify-content: initial;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  text-decoration: none;
  color: initial;
}

.home__content .footer a :hover {
  cursor: pointer;
}

.home__content .footer i {
  font-size: 2rem;
  color: #6256bf;
}

.home__content .footer:hover {
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
}

.home__content > div:nth-child(1) {
  height: 18rem;
}

.home__content a {
  text-decoration: none;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.home__profile {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  position: relative;
  /* img {
        // max-width: 60%;
    } */
}

